.cartcheckoutsignin__body {
  width: 100%;
  border: 1px solid rgb(201, 199, 199);
  padding: 20px 10px;
  background: #f5f5f5;
  opacity: 0.8;
}
.cartcheckoutsignin__username {
  border: 1px solid rgb(201, 199, 199);
  width: 100%;
  padding: 10px;
  background: white;
  font-size: large;
}
.cartcheckoutsignin__username > input {
  width: 100%;
  border: none;
  outline-width: 0;
}
.cartcheckoutsignin__password {
  margin-top: 5px;
  border: 1px solid rgb(201, 199, 199);
  width: 100%;
  padding: 10px;
  background: white !important;
  font-size: large;
  display: flex;
}
.cartcheckoutsignin__password > input {
  width: 100%;
  border: none;
  outline-width: 0;
  flex-grow: 1;
}
.cartcheckoutsignin__middle-forgotpassword {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.cartcheckoutsignin__middle-forgotpassword > h6 {
  margin: 5px;
}
.cartcheckoutsignin__login {
  display: flex;
  justify-content: center;
  border: 1px solid;
  border-radius: 2px;
  width: 100%;
  font-weight: bolder;
  cursor: pointer;
  align-items: center;
  transition: all 0.5s ease-in-out;
  padding: 15px 10px;
  margin: 10px 0;
  color: white;
  background-color: black;
}

.cartcheckoutsignin__login:hover {
  color: black;
  background-color: white;
}

.cartcheckoutsignin__login-asguest {
  display: flex;
  justify-content: center;
  border: 1px solid;
  border-radius: 2px;
  width: 100%;
  align-items: center;
  padding: 15px 10px;
  margin: 10px 0;
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  margin: 0;
  color: black;
  background-color: white;
}
.cartcheckoutsignin__login-asguest:hover {
  color: white;
  background-color: black;
}
.cartcheckoutsignin__checkout {
  display: flex;
  justify-content: space-around;
  border: 1px solid;
  border-radius: 2px;
  width: 100%;
  align-items: center;
  padding: 7px 15%;
  margin: 10px 0;
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  margin: 10px 0;
  color: black;
  background-color: white;
}
.cartcheckoutsignin__checkout:hover {
  color: white;
  background-color: black;
}
.cartcheckoutsignin__checkout:hover > img {
    background-color:black
}
