.CartItemsWishList__details {
  padding: 10px;
  overflow: hidden;
  height: 100%;
}
.CartItemsWishList__details-topsection {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 5px;
}
.CartItemsWishList__details-topsection-flex-grow {
  flex: 1;
}
.CartItemsWishList__details-card {
  width: 300px;
  margin: 10px;
  box-shadow: 0 16px 47px 0 rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  overflow: hidden;
  background: white;
}
.CartItemsWishList__details-TopImg > img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.CartItemsWishList__details-bottom{
    padding: 10px;
}