.c-profile {
    width: 30px;
    height: 30px;
    border: 1px solid white;
    border-radius: 50%;
    box-shadow: 0px 3px 8px rgba(44, 44, 84, .2);
    display: inline-block;
    background: white;
    cursor: pointer;
    background-size: cover;
    background-position: center center;
    transition: all 200ms ease;
}
.c-profile:nth-child(1){
    background-image: url("https://vignette.wikia.nocookie.net/arresteddevelopment/images/7/77/2x01_The_One_Where_Michael_Leaves_%28098%29.png/revision/latest?cb=20121126025806")
}
.c-profile:nth-child(2){
    background-image: url("https://vignette.wikia.nocookie.net/arresteddevelopment/images/1/16/Season_3_Character_Promos_-_Lindsay_Bluth_F%C3%BCnke_01.jpg/revision/latest?cb=20111027201233")
}
.c-profile:nth-child(3){
    background-image: url("https://vignette.wikia.nocookie.net/arresteddevelopment/images/b/be/Season_3_Character_Promos_-_Buster_Bluth_01.jpg/revision/latest?cb=20111027201440")
}
.c-profile:nth-child(4){
    background-image: url("https://vignette.wikia.nocookie.net/arresteddevelopment/images/c/c3/Season_1_Character_Promos_-_George_Michael_Bluth_02.jpeg/revision/latest?cb=20120429230332")
}
.c-profile:nth-child(5){
    background-image: url("https://vignette.wikia.nocookie.net/arresteddevelopment/images/0/02/Season_1_Character_Promos_-_G.O.B.jpeg/revision/latest?cb=20120429230530")
}
.c-profile:nth-of-type(n+2) {
    margin-left: -15px;
}
.c-profile:hover {
    transform: scale(1.2);
    box-shadow: 0px 8px 20px rgba(44, 44, 84, .2);
}
.c-profile:hover:after {
    position: absolute;
    content: attr(username);
    background: rgba(255, 255, 255, .95);
    color: inherit;
    font-size: 10px;
    padding: 4px;
    width: auto;
    bottom: -0.9rem;
    right: -0.1rem;
    box-shadow: 0px 5px 12px rgba(0, 0, 0, .12);
    opacity: 0;
    border-radius: 0.15rem;
    animation: fade 100ms ease 750ms forwards;
}
.c-profile__list {
    display: inline-flex;
    position: relative;
    width: auto;
    padding-top: 1rem;
    padding-bottom: 1rem;
   
}
.c-profile__list:hover .c-profile:nth-of-type(n+2) {
    margin-left: 2.5px;
}
article {
    width: 100%;
    max-width: 600px;
}
@keyframes fade {
    from {
        opacity: 0;
   }
    to {
        opacity: 1;
   }
}
