.product__display {
  display: flex;
  margin-top: 50px;
  margin-bottom: 50px;
}
.productDisplay__left-Sidebar {
  flex: 0.21;
  display: flex;
  flex-direction: column;
  /*margin-left: 30px;*/
  font-weight: bolder;
  opacity: 0.7;
  font-family: "IBM Plex Sans", sans-serif;
  line-height: 30px;
  align-items: center;
}
.productDisplay__Right-Sidebar {
  flex: 0.79;
}
.productDisplay__Right-Sidebar-data {
  display: flex;
  flex-wrap: wrap;
}
/*
.productDisplay__Right-Sidebar-data{
    display: flex;
    flex-wrap: wrap;
}*/
.my-masonry-grid {
  display: -webkit-box; /* Not needed if autoprefixing */
  display: -ms-flexbox; /* Not needed if autoprefixing */
  display: flex;
  width: auto;
}
.my-masonry-grid_column {
  background-clip: padding-box;
}

/* Style your items */
.my-masonry-grid_column > div {
  /* change div to reference your elements you put in <Masonry> */
  margin-bottom: 30px;
}

.productDisplay__button {
    display: flex;
    margin: auto;
    justify-content: center;
    border: 1px solid;
    border-radius: 2px;
    padding: 20px;
    width: 55%;
    font-weight: bolder;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
}

.productDisplay__button:hover{
  background-color:black;
  color:white;
}

