.Subscribe__letter {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 80px;
  text-align: center;
  background-color: #f2eeea;
}
.Subscribe__letter-heading {
  text-transform: uppercase;
  font-size: xx-large;
  font-weight: bolder;
}

.Subscribe__letter-description {
  font-weight: bold;
  letter-spacing: 1px;
  margin-bottom: 20px;
}

.Subscribe__letter-inputField {
  display: flex;
  width: 80%;
  justify-content: center;
}
.Subscribe__letter-inputField-input {
  font-size: x-large;
  border-right: none;
  width: 50%;
  font-style: oblique;
  opacity: 0.7;
}

.Subscribe__letter-inputField-button {
  display: flex;
  justify-content: center;
  border: 1px solid;
  border-radius: 2px;
  padding: 15px;
  font-weight: bolder;
  width: 40%;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  background-color: black;
  color: white;
}

.Subscribe__letter-inputField-button:hover {
  background-color: white;
  color: black;
}
