.data__display {
  display: flex;
  flex-direction: column;
  width: 270px;
  overflow: hidden;
  flex-wrap: wrap;
  margin: 10px;
  box-shadow: 0 16px 47px 0 rgba(0, 0, 0, 0.1);
  border-radius: 2px;
}

.data__display-TopImg {
  overflow: hidden;
}

.data__display-TopImg > img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.data__display-bottom {
    display:flex;
    flex-direction: column;
}

.data__display-Top-categoryType{
    margin: -8px 10px 0 10px;
    padding: 0px 10px;
    background-color: #B5A18A;
    cursor: pointer;
    color:white

}

.data__display-bottom{
  padding: 0px 10px;
}

.data__display-bottom-publisher{
  display: flex;
  padding: 5px 0px;
  align-items: center
}

.data__display-bottom-publisher > h5{
  margin-left: 5px;
}

.data__display-bottom-actions{
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.data__display-bottom-actions > p{
  font-weight: 1000;
}
.Icons{
  margin: 0 8px;
}