.left-menu-static-container {
  flex-direction: column;
  position: fixed;
  z-index: 10;
  position: fixed;
  top: 30%;
  left: 0;
  -webkit-transform: translateY(-50px);
  transform: translateY(-50px);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-flex-direction: column;
  flex-direction: column;
  list-style: none;
  overflow: hidden;
  width: 50px;
  margin: 0 0 0 15px;
  background: white;
  border-radius: 50px;
  padding: 10px 0;
  color: black !important;
  /*-webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  border-radius: 50px;*/
}

.fa-menu-icon {
  font-size: 30px;
  margin-bottom: 50px;
  color: #bcb6ae;
}
.selected {
  color: black;
}
