.company__functionalities{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px;
}
.company__functionalities-left{
    flex: 0.25;
    font-family: "IBM Plex Serif";
    font-size: 20px;
    margin: auto;
}
.company__functionalities-right{
    flex: 0.75;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.company__functionalities-right-column{
    width: 200px;
    height: 100px;
    border: 1px solid black;
    margin:10px;  
    border-radius: 1px;
    padding:8px;
}
.company__functionalities-right-column-heading{
    font-size:15px;
    width: 70%;
    line-height: 24px;
}