.productImg {
  width: 100%;
}

.productWrap {
  padding-top: 20px;
}

.productDetails h5 {
  margin: 0px;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: italic;
  font-family: "IBM Plex Serif";
}

.description {
  font-size: 12px;
  margin: 7px 0px;
}

.price {
  color: #000000;
  font-size: 13px;
  margin: 0px;
  font-weight: bold;
  font-family: "IBM Plex Sans", sans-serif;
}

.street {
  margin: 5px 0px;
  font-size: 12px;
  font-weight: 500;
  font-family: "IBM Plex Sans", sans-serif;
  color: #999999;
  text-align: justify;
}

.date {
  margin: 0px;
  color: #999999;
  font-size: 16px;
  font-weight: normal;
}

.btnWrap {
  display: flex;
}

.btn {
  color: #b5a18a;
  margin-right: 20px;
  font-size: 13px;
  font-weight: bold;
  font-family: "IBM Plex Sans", sans-serif;
}

.productOrder {
  padding: 10px 0px;
  border-bottom: 1px solid #eeeeee;
}

.price {
  color: #333333;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 14px;
  font-weight: bold;
}

.delivery {
  color: #333333;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 15px;
  font-weight: bold;
  margin: 0px;
}

.deliveryDate {
  color: #000000;
  font-style: italic;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 14px;
  margin: 0px;
  margin-left: 10px;
}
