.meet__artists {
  display: flex;
  padding: 50px;
  background: black;
  color: white;
  overflow: hidden;
}

.meet__artists-left {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex: 0.25;
  font-size: large;
  letter-spacing: 2px;
}
.meet__artists__button {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  justify-content: center;
  border: 1px solid;
  border-radius: 2px;
  padding: 20px;
  width: 55%;
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}
.meet__artists__button:hover{
  background-color:black;
  color:white;
}

.meet__artists-right {
  flex: 0.75;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.meet__artists-right-Top {
  overflow: hidden;
  width: 250px;
  margin: 8px;
}
.meet__artists-right > .meet__artists-right-Top > img {
  width: 100%;
  object-fit: contain;
}
.meet__artists-left > h1{
    line-height: 50px;
    font-family: IBMPlexSerif;
    font-size:55px;
}
.meet__artists-left > p{
    line-height:20px
}
