@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@600&display=swap");
.order__summary {
  width: 98%;
}
.order__summary-top {
}

.order__summary-top h2 {
  margin-top: 0px;
  font-family: "IBM Plex Serif";
  font-weight: 600;
  font-size: 18px;
  color: #333333;
  letter-spacing: 1px;
}
.order__summary-middleTop {
  border: 1px solid rgb(201, 199, 199);
  padding: 20px 10px;
  width: 100%;
}
.order__summary-middleTop-details {
  display: flex;
  justify-content: space-between;
}
.order__summary-middleTop-details > h4 {
  margin: 6px 0;
}

.lightFontText {
  font-weight: 100;
}
.order__summary-middleTop-details-total {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.order__summary-middleTop-details-total > h2 {
  font-weight: 900;
  margin: 0px;
}

.order__summary-middleBottom {
  border: 1px solid rgb(201, 199, 199);
  margin: 15px 0;
  display: flex;
  align-items: center;
  padding: 10px;
}
.order__summary-middleBottom > input {
  flex-grow: 1;
  outline: none;
  border-width: 0;
  padding: 5px;
  font-size: 15px;
  font-weight: bold;
}

.promocodeInput::placeholder {
  color: #333333;
}
