.company__profile {
  display: flex;
  color: black;
  background-color: white;
  padding: 60px 50px;
}

.company__profile-left {
  display: flex;
  flex-direction: column;
  flex: 0.25;
  justify-content: center;
  padding: 20px;
}
.company__profile-left > h1 {
  font-family: "IBM Plex Serif";
  font-weight: bold;
  font-size: 40px;
}
.company__profile-left > h2 {
  font-family: "IBM Plex Serif";
  font-size: x-large;
}
.company__profile-right {
  flex: 0.75;
  display: flex;
}
.company__profile-left-button {
  display: flex;
  justify-content: center;
  border: 1px solid;
  border-radius: 2px;
  width: 80%;
  padding: 15px;
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  background-color: black;
  color: white;
}

.company__profile-left-button:hover {
  background-color: white;
  color: black;
}

.company__profile-right-columns {
  padding: 10px;
  letter-spacing: 1px;
  margin:10px;
  line-height: normal;
}
.company__profile-right-columns > img {
  display: flex;
  justify-content: center;
  align-items: center;
  margin:auto;
}
