@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@500&display=swap");

.header {
  display: flex;
  justify-content: space-between;
  height: 40px;
  margin-bottom: 50px;
  margin-top: 50px;
}

.header img {
  height: 80%;
}

.tab {
  display: flex;
  border-bottom: 1px solid #eeeeee;
  padding-bottom: 10px;
  margin-bottom: 30px;
}

.tab p {
  margin: 0px;
  font-size: 27px;
  /* font-weight: bold; */
  letter-spacing: 0.63px;
  font-family: "IBM Plex Sans", sans-serif;
  color: #333333;
  cursor: pointer;
}

.tab span {
  margin-right: 20px;
  margin-left: 20px;
  font-size: 30px;
}
