.product__details-info {
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
}
.product__details-body {
  margin-top: 100px;
  margin-bottom: 50px;
  display: flex;
  justify-content: center;
  width: 80%;
}

.product__details-body-left {
  flex: 52;
  padding: 0 6px 0 0;
}

.product__details-body-right {
  flex: 48;
  padding: 0 0 0 6px;
}
.product__details-body-left-bottom-details {
  display: flex;
  flex-direction: column;
  font-weight: bolder;
}
.product__details-body-left-bottom-details-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.product__details-body-left-bottom-details-bottom{
  display: flex;
  font-weight: bold;
  align-items: center;
}
.product__details-body-left-bottom-details-bottom-text{
  margin: 0 5px;
  display: flex;
}
.product__details-body-left-bottom-details-upperMiddle {
  display: flex;
  flex-direction: column;
}
.product__details-body-left-bottom-details-upperMiddle-collab {
  display: flex;
}
.product__details-body-left-bottom-details-upperMiddle-collab-text {
  flex: 0.3;
}
.product__details-body-left-bottom-details-upperMiddle-collaboration {
  display: flex;
  flex-direction: column;
  flex: 0.7;
}
.product__details-body-left-bottom-details-upperMiddle-collabpeople {
  display: flex;
  align-items: center;
}
.product__details-body-left-bottom-details-upperMiddle-sponsors {
  display: flex;
}
.product__details-body-left-bottom-details-upperMiddle-sponsors-text {
  flex: 0.3;
}
.product__details-body-left-bottom-details-upperMiddle-sponsoredBy {
  display: flex;
  align-items: center;
  flex: 0.7;
}
.product__details-body-left-bottom-details-lowerMiddle {
  display: flex;
  margin: 10px 0;
}
.product__details-body-left-bottom-details-lowerMiddle-addproof {
  display: flex;
  justify-content: center;
  border: 1px solid;
  border-radius: 2px;
  width: 40%;
  align-items: center;
  padding: 15px;
  margin: 0 2% 0 0;
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  color: black;
  background-color: white;
}
.product__details-body-left-bottom-details-lowerMiddle-addproof:hover {
  color: white;
  background-color: black;
}
.roduct__details-body-left-bottom-details-lowerMiddle-requestproof:hover {
  color: black;
  background-color: white;
}
.product__details-body-left-bottom-details-lowerMiddle-requestproof {
  display: flex;
  justify-content: center;
  border: 1px solid;
  border-radius: 2px;
  width: 40%;
  align-items: center;
  padding: 15px;
  margin: 0 0 0 2%;
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  color: black;
  background-color: white;
}
.product__details-body-right-top-top {
  margin: 0 0 7px 0;
}

.product__details-body-right-top-top > h2 {
  margin: 5px 0px 10px 0px;
}
.product__details-body-right-top-top > span {
  background-color: #b5a18a;
  padding: 0.6% 1.1% 0.6% 1.1%;
  color: white;
  margin: 2px;
}
.product__details-body-right-top-middle-top {
  display: flex;

  flex-direction: column;
  margin: 20px 0 0 0;
}
.product__details-body-right-top-middle-top-top {
  display: flex;
  align-items: center;
}
.product__details-body-right-top-middle-top-top > a {
  border-left: 1px solid #b5818a;
  padding: 0 0 0 8px;
}
.product__details-body-right-top-middle-top > p {
  margin: 5px 0 0 0;
  font-size: x-small;
  opacity: 0.7;
}
.product__details-body-right-top-middle-top-top > h4,
a {
  margin: 0 0 0 8px;
}

.product__details-body-right-top-middle-top-top > a {
  color: #b5818a;
}
.product__details-body-right-top-middle-middle span {
  font-weight: bold;
  font-size: 110%;
}
.product__details-body-right-top-middle-middle {
  font-size: medium;
}
.product__details-body-right-top-middle-middle-productdetails {
  display: flex;
}
.product__details-body-right-top-middle-middle-productdetails-rows {
  margin-right: 20px;
  border-right: 1px solid #b5818a;
  padding-right: 20px;
}
.product__details-body-right-top-middle-middle-productdetails-rows-location {
  margin: 0;
}
.product__details-body-right-top-middle-middle-productdetails-rows-geolocation {
  margin: 5px 0 0 0;
}
.product__details-body-right-top-middle-middle-productdetails-rows-geolocation
  > a {
  color: #b5818a;
}
.product__details-body-right-top-middle-middle-productdetails-rows-countryinfo {
  display: flex;
  flex-direction: column;
}
.product__details-body-right-top-middle-middle-productdetails-rows-countryinfo
  > h5:nth-child(1) {
  margin: 0;
}
.product__details-body-right-top-middle-middle-productdetails-rows-countryinfo
  > h5:nth-child(2) {
  margin: 5px 0 0 0;
}

.product__details-body-right-top-middle-bottom {
  margin: 20px 0 0 0px;
  display: flex;
}
.product__details-body-right-top-bottom {
}
.product__details-body-right-top-bottom-buyNow {
  display: flex;
  justify-content: center;
  border: 1px solid;
  border-radius: 2px;
  width: 48%;
  font-weight: bolder;
  cursor: pointer;
  align-items: center;
  transition: all 0.5s ease-in-out;
  padding: 20px 30px;
  margin: 0 5% 0 0;
  color: white;
  background-color: black;
}
.product__details-body-right-top-bottom-buyNow:hover {
  color: black;
  background-color: white;
}
.product__details-body-right-top-bottom-addToCart {
  display: flex;
  justify-content: center;
  border: 1px solid;
  border-radius: 2px;
  width: 48%;
  align-items: center;
  padding: 10px;
  margin: 0 0 0 5%;
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  margin: 0;
  color: black;
  background-color: white;
}
.product__details-body-right-top-bottom-addToCart:hover {
  color: white;
  background-color: black;
}

.product__details-body-right-bottom {
  margin: 15px 0 0 0;
}
.product__details-body-right-bottom-top {
  background: #f2eeea;
  text-align: center;
  padding: 15px;
  font-weight: bolder;
}

.product__details-body-right-top-bottom-actions {
  display: flex;
  color: #b5818a;
  font-weight: bolder;
  margin: 5px 0;
}
.product__details-body-right-top-bottom-actions-features {
  display: flex;
  align-items: center;
}
.product__details-body-right-bottom-top-viewdetails {
  line-height: 20px;
  font-weight: bolder;
}

@media (min-width: 768px) {
  .change {
    margin: auto 10px;
    height: 5px;
  }
}
